import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import How from "../views/How.vue";
import Blog from "../views/Blog.vue";
import Account from "../views/Account.vue";
import Viewplans from "../views/View_plans.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/How",
    name: "How",
    component: How,
  },
  {
    path: "/Blog",
    name: "Blog",
    component: Blog,
  },
  {
    path: "/Account",
    name: "Account",
    component: Account,
  },
  {
    path: "/View_plans",
    name: "View_plans",
    component: Viewplans,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
