<template>
  <div class="Post">
    <div class="Child form-check form-switch" style="width: calc(100% - 106px)">
      <input
        type="checkbox"
        class="form-check-input"
        @click="changeCom"
        :checked="completed"
        :id="'Id' + id"
      />
      <label :for="'Id' + id">{{ Title | CutString(45) }}</label>
    </div>
    <div class="Child" style="width: 106px">
      <button @click="Edit" class="Child">
        <icon icon="fa-solid fa-pen-to-square" />
      </button>
      <button @click="Del" class="Child">
        <icon icon="fa-solid fa-trash-can" />
      </button>
    </div>
    <div :class="'prompt ' + ClassPrompt">
      <div :class="'ChildPrompt ' + ClassPrompt1">
        <h1 class="mb-5">Edit To Do</h1>
        <input type="text" v-model="Input" />
        <div>
          <button class="btn btn-primary" @click="OK">
            <icon icon="fa-solid fa-floppy-disk" class="me-2" /> Save
          </button>
          <button class="btn btn-secondary" @click="ClassPrompt = ''">
            <icon icon="fa-solid fa-angle-left" class="me-2" />cancel
          </button>
        </div>
      </div>
      <div :class="'ChildPrompt ' + ClassPrompt2">
        <h1 class="mb-1">Delete To Do</h1>
        <div class="p-3 bg-danger text-white fs-5 text-center text-capitalize">
          You Are Delete The {{ Title }}
        </div>
        <div>
          <button class="btn btn-danger" @click="RemoveFromArray">
            <icon icon="fa-solid fa-trash-can" class="me-2" />Delete
          </button>
          <button class="btn btn-secondary" @click="ClassPrompt = ''">
            <icon icon="fa-solid fa-angle-left" class="me-2" />cancel
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Post',
  data: function () {
    return {
      ClassPrompt: '',
      ClassPrompt1: '',
      ClassPrompt2: '',
      Input: '',
    };
  },
  props: ['id', 'Title', 'completed', 'All'],
  methods: {
    changeCom: function () {
      if (this.completed) {
        this.completed = false;
      } else {
        this.completed = true;
      }
      console.log(this.completed);
    },
    Edit: function () {
      this.ClassPrompt = 'Active';
      this.ClassPrompt1 = 'Edit';
      this.ClassPrompt2 = '';
      this.Input = this.Title;
    },
    Del: function () {
      this.ClassPrompt = 'Active';
      this.ClassPrompt2 = 'Delete';
      this.ClassPrompt1 = '';
      this.Input = this.Title;
    },
    RemoveFromArray: function () {
      for (var i = 0; i < this.All.length; i++) {
        if (this.All[i].id == this.id) {
          this.All.splice(i, 1);
          break;
        }
      }
      this.ClassPrompt = '';
      this.ClassPrompt2 = '';
      this.ClassPrompt1 = '';
    },
    OK: function () {
      this.Title = this.Input;
      this.ClassPrompt = '';
    },
  },
};
</script>
